import { Component, Event, EventEmitter, Prop, h } from '@stencil/core';
import { ICON_SIZES } from '../../icons/exports';
import { STEP_STATES } from './exports';

/**
 * @slot content - content of a single element
 * @slot button - button(s) of a single element
 * @slot summary - summary of the step if stepSummaryEnabled is true step is done
 */

@Component({
  tag: 'mdx-progressive-disclosure-item',
  styleUrl: 'progressive-disclosure-item.scss',
  shadow: true,
})
export class ProgressiveDisclosureItem {
  @Prop({ reflect: true })
  state: (typeof STEP_STATES)[keyof typeof STEP_STATES] = STEP_STATES.TODO;

  @Prop()
  step: number;

  @Prop()
  stepName: string;

  @Prop()
  contentTitle: string;

  @Event()
  progressiveDisclosureClick: EventEmitter<{
    state: (typeof STEP_STATES)[keyof typeof STEP_STATES];
    step: number;
  }>;

  private isTodo() {
    return this.state === STEP_STATES.TODO;
  }

  private isActive() {
    return this.state === STEP_STATES.ACTIVE;
  }

  private isDone() {
    return this.state === STEP_STATES.DONE;
  }
  render() {
    return (
      <details
        class="progressive-disclosure-item"
        open={this.isActive()}
        aria-expanded={this.isActive()}
        onClick={() => {
          if (this.isTodo() || this.isDone()) {
            this.state = STEP_STATES.ACTIVE;
            this.progressiveDisclosureClick.emit({ state: this.state, step: this.step });
          }
        }}
      >
        <summary
          class="progressive-disclosure-item__summary-container"
          onClick={(e) => {
            e.preventDefault();
          }}
        >
          <div class="progressive-disclosure-item__summary">
            <div class="progressive-disclosure-item__summary-step">
              {this.isDone() ? (
                <mdx-icon-check-circle class="progressive-disclosure-item__done-icon" size={ICON_SIZES.SM} />
              ) : (
                <div class="progressive-disclosure-item__todo-icon-container">
                  <mdx-icon-circle class="progressive-disclosure-item__todo-icon" size={ICON_SIZES.SM} />
                  <span class="progressive-disclosure-item__todo-step progressive-disclosure-item--font-weight">
                    {this.step}
                  </span>
                </div>
              )}
            </div>
            <div class="progressive-disclosure-item__summary-step-name progressive-disclosure-item--font-weight">
              {this.stepName}
            </div>
            {this.isDone() && <slot name="summary" />}
          </div>
          {this.isDone() && <mdx-icon-chevron-down class="progressive-disclosure-item__chevron" />}
        </summary>
        <div class="progressive-disclosure-item__content-container">
          <div class="progressive-disclosure-item__content-title">{this.contentTitle}</div>
          <slot name="content" />
          <mdx-button-group>
            <slot name="button" />
          </mdx-button-group>
        </div>
      </details>
    );
  }
}
