:host .progressive-disclosure-item {
  box-sizing: border-box;
  background-color: var(--mdx-comp-pd-background-color-default);
  border-radius: var(--mdx-comp-pd-border-radius-default);
  border-color: var(--mdx-comp-pd-border-color-default);
  box-shadow: var(--mdx-comp-pd-box-shadow-default);
  font: var(--mdx-comp-pd-font-default);
  display: flex;
  cursor: pointer;

  &__summary-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: var(--mdx-comp-pd-padding-horizontal-default) var(--mdx-comp-pd-padding-vertical-default);

    &:focus-visible {
      outline-style: solid;
      border-radius: var(--mdx-comp-keyboard-focus-border-radius-default);
      outline-width: var(--mdx-comp-keyboard-focus-border-width-default);
      outline-color: var(--mdx-comp-keyboard-focus-color-default);
    }
  }

  &__summary {
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: repeat(2, auto);
    grid-column-gap: var(--mdx-comp-pd-gap-step-indicator-title);
    align-items: center;
    color: var(--mdx-comp-pd-short-title-color-default);
  }

  &__summary-step {
    grid-area: 1 / 1 / 2 / 2;
  }

  &__summary-step-name {
    grid-area: 1 / 2 / 2 / 3;
  }

  ::slotted([slot='summary']) {
    grid-area: 2 / 2 / 3 / 3;
    margin-top: var(--mdx-comp-pd-gap-step-name-to-summary);
  }

  &__chevron {
    height: var(--mdx-comp-pd-sizing-icon);
    width: var(--mdx-comp-pd-sizing-icon);
    color: var(--mdx-comp-pd-chevron-color-default);
  }

  &__done-icon {
    color: var(--mdx-comp-pd-state-indicator-color-done);
  }

  &__todo-icon-container {
    position: relative;
  }

  &__todo-icon {
    color: var(--mdx-comp-pd-state-indicator-color-todo);
  }

  &__todo-step {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: var(--mdx-comp-pd-state-indicator-color-todo);
  }

  &__content-container {
    padding: var(--mdx-comp-pd-padding-vertical-content-top) var(--mdx-comp-pd-padding-horizontal-default)
      var(--mdx-comp-pd-padding-vertical-content-bottom) var(--mdx-comp-pd-padding-horizontal-default);
  }

  &__content-title {
    color: var(--mdx-comp-pd-long-title-color-default);
    font: var(--mdx-comp-pd-font-title);
    margin-bottom: var(--mdx-comp-pd-margin-title-bottom);
  }

  &--font-weight {
    font-weight: var(--mdx-comp-pd-font-weight-todo);
  }
}

:host([state='active']) .progressive-disclosure-item {
  cursor: default;
  &__todo-icon {
    color: var(--mdx-comp-pd-state-indicator-color-active);
  }

  &__todo-step {
    color: var(--mdx-comp-pd-state-indicator-color-active);
  }

  &--font-weight {
    font-weight: var(--mdx-comp-pd-font-weight-active);
  }
}

:host([state='done']) .progressive-disclosure-item {
  &__summary-container:hover {
    background-color: var(--mdx-comp-pd-background-color-hover);
  }

  &--font-weight {
    font-weight: var(--mdx-comp-pd-font-weight-done);
  }
}

details > summary::-webkit-details-marker {
  display: none;
}

::slotted([slot='content']) {
  margin-bottom: var(--mdx-comp-pd-margin-content-bottom);
}